<template>
  <section>
    <section
      v-if="!parsed"
      class="explore__loader"
    >
      <div class="lds-ellipsis">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
    </section>
    <TeamView
      v-else
      :parsed='parsed'
    />
  </section>
</template>

<script>
import axios from "axios";
import { parseTeam } from "@/services/teamParser";
import TeamView from "@/components/TeamView";

export default {
  title: 'Team Viewer',
  components: {
    TeamView
  },
  data() {
    return {
      parsed: false,
      tiers: {
        ou: 100,
        vgc: 50,
        bss: 50,
        ru: 100,
        nu: 100,
        ubers: 100,
        monotype: 100,
        uu: 100
      }
    };
  },
  created() {
    const id = this.$route.params.id;
    const tier = this.$route.params.tier;

    axios.post("/breakmyteam/shared", { id: id }).then(res => {
      if (res.data.code) {
        let parsed = {};
        parsed.pokemon = parseTeam(res.data.team.raw);
        parsed.tier = { defaultLevel: this.tiers[tier], alias: tier };
        parsed.raw = res.data.team.raw
        
        for (let index in parsed.pokemon) {
          if (parsed.pokemon[index].level === undefined)
            parsed.pokemon[index].level = this.tiers[tier];
        }

        axios.post("/breakmyteam", parsed).then(res => {
          if (res.data.sucess) {
            this.parsed = res.data.response;
          } else {
            alert(res.data.message);
          }
        });
      }
    });
  }
};
</script>